@import url(https://fonts.googleapis.com/css2?family=Chakra+Petch:wght@400;500&display=swap);
#root {
    font-family: 'Chakra Petch', sans-serif;
    font-weight: 500;
}

#wrapper {
    background-color: #1a1a1a;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    padding: 1rem;
    margin: auto;
}

#display-wrapper {
    background-color: #3b3b3b;
    border-radius: 0.35rem;
    height: 80px;
    margin-bottom: 0.5rem;
}

#display {
    padding: 0 5px 0 5px;
    color: white;
}

#result {
    padding: 0 5px 0 5px;
    color: white;
    text-align: right;
    font-size: 20px;
}

#button-wrapper {
    display: -webkit-flex;
    display: flex;
}

#numerical-container {
    width: 240px;
}

#operation-container {
    width: 160px;
}

.button {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    color: black;
    height: 65px;
    border-radius: 0.35rem;
    background-color: white;
    font-weight: 500;
}

.blue {
    color: white;
    background-color: #1048ff;
}
